<template>
  <div class="row mt-2 mb-2">
    <div class="col-md-4 col-6">
      <span class="text-secondary">{{ payload.label }}</span>
    </div>
    <div class="col-md-8 col-6">
      <i v-for="n in 5" :key="n"
      :class="getStarClass(n)" :style="{color: color}"></i>
    </div>
  </div>
</template>

<script>
export default {
  name: 'StarScore',
  props: {
    payload: {
      type: Object,
      default() {
        return {
          label: '라벨',
          score: 5
        }
      }
    },
    color: {
      type: String,
      default: '#000000'
    }
  },
  methods: {
    getStarClass (inputScore) {
      const score = this.payload.score - inputScore      
      if(score >= 0) {
        return ['mdi mdi-star']
      }else if (score < 0 && Math.abs(score) < 1){
        return ['mdi mdi-star-half-full']
      }else{
        return ['mdi mdi-star-outline']
      }
    }
  }
}
</script>


