<template>
  <div class="parallex" :style="{backgroundImage: `url(${parallexImage})`}">
    <div class="fixed-navbar fixed-top" :class="{'show': showFixedNavbar}" ref="fixedNavbar">
      <navbar/>
    </div>
    <section class="bg-dark-a5" id="profile" ref="profile">
      <navbar :theme="'white'"/>
      <div class="profile pt-5">
        <div class="container">
          <div class="row">
            <div class="col-md-3 col-6 ml-auto mr-auto mb-5">
              <img class="profile-img" src="@/assets/images/profile.jpg"/>
            </div>
            <div class="col-md-9 mb-5">
              <div class="b-line">
                <div class="row">
                  <label class="col-3 col-form-label">Name</label>
                  <div class="col-9 pt-2">서강민</div>
                </div>
              </div>
              <div class="b-line">
                <div class="row">
                  <label class="col-3 col-form-label">Birth</label>
                  <div class="col-9 pt-2">1980.03.19</div>
                </div>
              </div>
              <div class="b-line">
                <div class="row">
                  <label class="col-3 col-form-label">Address</label>
                  <div class="col-9 pt-2">서울시 중구 장충동2가 186-1</div>
                </div>
              </div>
              <div class="b-line">
                <div class="row">
                  <label class="col-3 col-form-label">Email</label>
                  <div class="col-9 pt-2 font-fm-">rivermin.seo@gmail.com</div>
                </div>
              </div>
              <div class="b-line">
                <div class="row">
                  <label class="col-3 col-form-label">Link</label>
                  <div class="col-9">
                    <div class="row">
                      <div class="col-md-6">
                        <a target="_blank" href="https://www.facebook.com/seogangmin86" class="btn btn-block text-left text-white">
                          <i class="mdi mdi-facebook"></i>
                          Facebook
                        </a>
                      </div>
                      <div class="col-md-6">
                        <a href="#" target="_blank" class="btn btn-block text-left text-white">
                          <i class="mdi mdi-github"></i>
                          Github
                        </a>
                      </div>
                    </div>                    
                  </div>
                </div>
              </div>              
            </div>
          </div>
        </div>
      </div>    
    </section> 
    <vue-aos animation-class="fade-in-up">  
      <section class="bg-white mb-3 pt-5 pb-5" id="skills" ref="skills">
        <div class="container">
          <div class="section-title">Skills</div>
          <div class="row">
            <div class="col-md-4 mb-3" v-for="(value, key) of skills" :key="key">
              <div>
                <strong>{{ key }}</strong>
                <star-score v-for="(item, index) of value" 
                  :key="index" 
                  :payload="item" 
                  :color="'#ff6b6b'"/>     
              </div>
            </div>            
          </div>
        </div>
      </section>
    </vue-aos> 
    <vue-aos animation-class="fade-in-up">
      <section class="bg-white mb-3 pt-5 pb-5" id="history" ref="history">
        <div class="container">
          <div class="section-title">History</div>        
          <timeline :items="history" :theme="'timeline-centered'"/>
        </div>
      </section>  
    </vue-aos>      
    <vue-aos animation-class="fade-in-up">
      <section class="bg-white pt-5 pb-5" id="experience" ref="experience">
        <div class="container">
          <div class="section-title">Introduce</div>
          <div>
            
          </div>
        </div>
      </section>
    </vue-aos>
    <section class="footer pt-3 pb-3 bg-dark-a5">
      <div class="container">
        <div class="text-center text-white text-shadow-black font-fm-lob">
          <strong>Created By me</strong>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import VueAos from 'vue-aos'

import Navbar from './ui/Navbar'
import Timeline from './ui/Timeline'
import StarScore from './ui/StarScore'
import pageData from '@/assets/data'
export default {
  name: 'HomePage',
  components:{
    VueAos,
    Navbar,    
    Timeline,
    StarScore
  },
  data () {
    return {
      parallexImage: null,
      showFixedNavbar: false,      
      headerHeight: 0,
      history: pageData.history,
      skills: pageData.skills,      
      experience: pageData.experience,      
    }
  },
  mounted () {    
    this.parallexImage = require((window.screen.width > 480) ? '@/assets/images/bg-parallex-pc.jpg' : '@/assets/images/bg-parallex-mob.jpg')
    this.headerHeight = this.$refs.profile.clientHeight          
    
    window.addEventListener('scroll', this.scrollListener)
  },
  methods: {
    scrollListener (e) {
      const scrollY = window.scrollY || document.documentElement.scrollTop
      this.showFixedNavbar = (scrollY > this.headerHeight)      
    }
  }
}
</script>

<style lang="scss" scoped>
.container{
  max-width: 960px;
}

.fixed-navbar{
  top: -200px;
  transition: top .3s;
  &.show{
    top: 0;
  }
}

.profile{
  color: white;
  .profile-img{
    width: 100%;
    border-radius: 50%;
  }
}

.b-line{
  border-bottom: 1px solid rgba(255, 255, 255, .5);
}

section{  
  .section-title{
    text-align: center;
    font-size: 2rem;
    font-weight: 700;
    margin-bottom: 1rem;    
    padding-bottom: .5rem;
    border-bottom: 1px solid #ddd;
    font-family: 'Roboto', sans-serif;
  }
  .fade{
    transition: opacity .3s ease-in-out;
    &.in{
      opacity: 1;
    }
  }
}

.parallex{  
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;  
}
.center-board{
  color: white;
  text-align: center;
  width: 100%;  
  .short-desc{
    display: flex;
    width: 200px;
    margin: 0 auto;
    .v-bar{
      display: flex;
      flex-grow: 1;
      align-items: center;
      div{
        width: 100%;
        height: 2px;
        background-color: white;
      }
    }
    .txt{
      display: flex;
      flex-grow: 2;
      justify-content: center;
    }
  }   
}
.page-container{  
  position: fixed;  
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
  overflow-y: auto;
  .top-bar{
    border-bottom: 1px soild #ddd;
    background-color: white;  
    padding: .5rem 0;  
    opacity: 0;
  }
  .page-content{        
    background-color: #efefef;    
  }
}


@media screen and (max-width: 480px) {    
  #skills{
    .row>[class^=col] {
      text-align: center;
    }
  }
}

</style>



