<template>
  <div class="nav-bar" :class="[theme]">
    <div class="container">
      <div class="row d-md-flex d-none">
        <div class="col-3">
          <div class="h-100 d-flex align-items-center">
            <h4 class="font-fm-ks">Rivermin's</h4>
          </div>
        </div>
        <div class="col-9 text-right">
          <a href="javascript:;" class="btn" v-scroll-to="'#profile'">
            Profile
          </a>          
          <a href="javascript:;" class="btn" v-scroll-to="'#skills'">
            Skills
          </a>
          <a href="javascript:;" class="btn" v-scroll-to="'#history'">
            History
          </a>          
        </div>
      </div>
      <div class="d-md-none d-block">
        <h4 class="font-fm-ks text-center">Rivermin's</h4>
        <div class="row">
          <div class="col">
            <a href="javascript:;" class="btn btn-sm btn-block" v-scroll-to="'#profile'">
              Profile
            </a>
          </div>
          <div class="col">
            <a href="javascript:;" class="btn btn-sm btn-block" v-scroll-to="'#skills'">
              Skills
            </a>
          </div>
          <div class="col">
            <a href="javascript:;" class="btn btn-sm btn-block" v-scroll-to="'#history'">
              History
            </a>
          </div>          
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'NavBar',
  props: {
    theme: {
      type: String,
      defaut: 'white'
    }
  }
}
</script>

<style lang="scss" scoped>
.nav-bar{
  padding: .75rem 0;
  border-bottom: 1px solid #000;  
  background-color: white;
  .btn{    
    &:hover{
      opacity: .5;
      transition: opacity .2s ease-in-out;
    }
  }
}
.nav-bar.white{
  color: white;
  background-color: transparent;
  border-bottom: 1px solid white;
  .btn{
    color: white;
  }
}
</style>


